// ## fonts
// Save fonts to /src/fonts
// +font(book, cytiapro-black-webfont)
// and create mixin
// =book
// 	font-family: "book", Arial, sans-serif

// fonts
+font(helvetica-light, helvetica-light)
+font(helvetica-medium, helvetica-medium)

=light
	font-family: "helvetica-light", Helvetica, sans-serif
=med
	font-family: "helvetica-medium", Helvetica, sans-serif

// media
=r1
	@media only screen and (max-width: 1020px)
		@content

=r2
	@media only screen and (max-width: 920px)
		@content

=r3
	@media only screen and (max-width: 660px)
		@content

// blocks
body
	min-width: 320px
	font-size: 18px
	line-height: 1.2
	letter-spacing: 0.05em
	+light

.out
	overflow: hidden


.h2
	font-size: 48px